@media only screen and (max-width: 720px) {

  .header-section {
    padding: 5vmin 0 0 0;

    h1 {
      padding: 1.5vmin 0;
    }

    details {
      margin: 3vmin 0 0 0;
      padding: 1vmin 1vmin;
    }

  }

  img.logo {
    max-width: 80px;
    padding: 1vmin 0;
  }

  .amount-span {
    padding: 4vmin 0 2vmin 0;
  }

  
  section input[type=number] {
    padding: 0 1vmin;
  }
}